import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Cadastro from '@/pages/Cadastro'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/cadastro',
      name: 'Cadastro',
      component: Cadastro
    }
  ]
})



